import {useEffect, useState} from "react";
import {useParams} from "react-router-dom";
import LoggedInContanier from './Contanier/LoggedInContanier'
import {AuthenticatedGetRequest} from "../Utils/ServerHelper";
import SingleSongCard from "./Share/SingleSongCard";

const PlaylistSong = () => {
    const [playlistDetails, setPlaylistDetails] = useState({});
    const {playlistId} = useParams();

    useEffect(() => {
        const getData = async () => {
            const response = await AuthenticatedGetRequest(
                "/api/playlist/get/playlist/" + playlistId
            );
            setPlaylistDetails(response);
            // console.log(response);
        };
        getData();
    }, []);

    return (
        <LoggedInContanier curActiveScreen={"library"}>
            {playlistDetails._id && (
                <div>
                    <div className="text-white text-xl pt-8 font-semibold">
                        {playlistDetails.name}
                    </div>
                    <div className="pt-10 space-y-3">
                        {
                            playlistDetails.songs.map((item) =>{
                                return(
                                    <SingleSongCard
                                    info={item}
                                    key={JSON.stringify(item)}
                                    playSound={()=>{}}
                                    />
                                )
                            })
                        }
                    </div>
                </div>
            )}
        </LoggedInContanier>
    );
};

export default PlaylistSong;
